.deck {
  font-family: "Titillium Web", sans-serif;
  font-family: "Dancing Script", cursive;
  font-size: 1.2rem;
}

h1 {
  font-weight: 700;
  font-size: 3rem;
  margin-bottom: 0;
}

.deck-btn {
  display: block;
  padding: 1em 1.5em;
  margin: 2rem auto;
  letter-spacing: 3px;
  background: #34be82;
  outline: none;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  color: #f0e9d2;
  transition: all 250ms;
}

.deck-btn:hover {
  filter: brightness(110%);
  cursor: pointer;
  font-size: 1.05rem;
  background: #1e5128;
}
